@use '../../helpers/functions' as *;

.back-section{
    background-color: #f1f1f1;
}
.ScreenContainer {
    position: relative;
    overflow: hidden;
    cursor: pointer;


    transition: 0.3s linear all;
    border: solid transparent 6px;


    &:hover {
        border-color: $BaseColor;
    }
}